@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}
.visually-hidden-focusable:not(:focus):not(:focus-within) {
  clip: rect(0, 0, 0, 0) !important;
  border: 0 !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

body {
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  letter-spacing: 0.2px;
}

.container {
  max-width: 1134px;
  margin: 0 auto;
  padding: 0 42px;
}

.btn {
  padding: 10px 36px;
  color: #FFFFFF;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 2;
  letter-spacing: 0.2px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  border: none;
}
.btn:hover {
  opacity: 0.8;
}
.btn.btn-primary {
  background-color: #FFA62B;
  border-radius: 5px;
  padding: 15px 40px;
}
.btn.btn-secondary {
  background-color: #00A0C1;
  border-radius: 37px;
}
.btn.btn-ghost {
  background-color: transparent;
  border-radius: 37px;
  border: 1px solid #00A0C1;
  color: #00A0C1;
}
.btn:disabled {
  background-color: #BDBDBD;
  cursor: not-allowed;
}
.btn.loading {
  position: relative;
}
.btn.loading:before {
  content: "";
  position: absolute;
  z-index: 5;
  top: 30%;
  right: 20px;
  width: 24px;
  height: 24px;
  border: 4px solid #FFFFFF;
  border-bottom-color: #FFA62B;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  background-color: transparent;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
label {
  display: block;
}
label span {
  display: inline-block;
  margin-bottom: 10px;
  color: #252B42;
  font-weight: 600;
  line-height: 1.7;
  cursor: pointer;
}

input[type=text],
input[type=email],
select {
  border-radius: 5px;
  border: 1px solid #BDBDBD;
  background: #F9F9F9;
  padding: 10px 20px;
  color: #737373;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  width: 100%;
  line-height: 2;
  margin-bottom: 7px;
  height: 50px;
}

.error-msg {
  color: red;
}

.success-msg {
  color: #14ae5c;
}

.form-msg {
  text-align: center;
  margin-top: 16px;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  line-height: 1.42;
  color: #252B42;
}

h1, .h1 {
  font-size: 58px;
}

h2, .h2 {
  font-size: 40px;
}

h3, .h3 {
  font-size: 24px;
}

h4, .h4 {
  font-size: 20px;
}

h5, .h5 {
  font-size: 16px;
}

h6, .h6 {
  font-size: 14px;
}

#header {
  height: 104px;
  padding: 23px 59px;
  position: absolute;
  width: 100%;
}
#header .sprivaten-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#header .sprivaten-nav .sprivaten-menu-nav {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  grid-gap: 21px;
}
#header .sprivaten-nav .sprivaten-menu-nav a {
  color: #737373;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.7;
  letter-spacing: 0.2px;
  text-decoration: none;
}
#header .sprivaten-nav .sprivaten-menu-nav a:hover {
  opacity: 0.8;
}

.section-hero {
  min-height: 883px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 104px;
}
.section-hero .hero-wrap {
  padding: 50px 0;
  display: flex;
  justify-content: space-between;
  grid-gap: 27px;
}
.section-hero .hero-wrap .hero-content-wrap {
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 35px;
}
.section-hero .hero-wrap .hero-content-wrap h4 {
  color: #737373;
  font-weight: 500;
}
.section-hero .hero-wrap .hero-content-wrap .buttons-wrap {
  display: flex;
  grid-gap: 10px;
}
.section-hero .hero-wrap .form-wrap {
  padding: 40px;
  background-color: #fff;
  border-radius: 10px;
  max-width: 328px;
  width: 100%;
}
.section-hero .hero-wrap .form-wrap h3 {
  margin-bottom: 40px;
}
.section-hero .hero-wrap .form-wrap .form-item-wrap {
  margin-bottom: 5px;
}
.section-hero .hero-wrap .form-wrap .form-item-wrap.no-valid .form-item-field {
  border-color: red;
}
.section-hero .hero-wrap .form-wrap .btn {
  margin-top: 40px;
  width: 100%;
}